.calloutbox {
  display: flex;
  flex-wrap: wrap;
}
.hideDesk {
  display: none;
  width: 100%;
  text-align: center;
  font-weight: 800;
}
.hide {
  display: block;
}

@media only screen and (max-width: 600px) {
  .hide {
    display: none !important;
  }
  .hideDesk {
    display: block;
  }
}
